<template>
  <el-card class="box-card">
    <el-form ref="dataForm" :model="param" :rules="rule" label-width="80px">
      <el-form-item label="barcode" prop="barcode">
        <el-input v-model="param.barcode" placeholder="barcode"></el-input>
      </el-form-item>
      <el-form-item label="iname" prop="iname">
        <el-input v-model="param.iname" placeholder="iname"></el-input>
      </el-form-item>
      <el-form-item label="weight" prop="weight">
        <el-input type="number" v-model="param.weight" placeholder="weight"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">confirm</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  data(){
    return {
      param: {
        barcode: '',
        iname: '',
        weight: '',
      },
      rule: {
        barcode: [{required: true, message: "please fill in barcode", trigger: 'blur'}],
        iname: [{required: true, message: "please fill in iname", trigger: 'blur'}],
        weight: [{required: true, message: "please fill in weight", trigger: 'blur'}],
      }
    }
  },
  methods: {
    onSubmit() {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$axios.post('/ingredients/create', this.param).then(res => {
            if (res.success) {
              this.$message({
                message: 'successfully added',
                type: 'success'
              });
              this.$router.back();
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    }
  }
}
</script>

